h1.title,
h1.author-title,
#tribe-community-events .my-events-header h2.my-events,
.single-tribe_events h1.tribe-events-single-event-title {
    font-size: $font-size-h1-content;
    line-height: ($font-size-h1-content*1.2);
    font-weight: $font-weight-light;
    letter-spacing: -0.2px;
    text-transform: none;
    padding: 12px 0 10px 0;
    margin-left: 0
}

.page-id-8038 h1.title {
    font-family: $font-family-boost;
    font-weight: $font-weight-medium;
    color: $color-boost;
    font-style: italic;
    font-size: 4.5rem;
}


#front-content-box h2 {
    font-size: 2.6rem;
    line-height: 2.6rem;
    font-family: $font-family;
    font-weight: $font-weight-light;
    letter-spacing: -2px;
    margin-left: 0;
    padding: 0 5px 30px 0;
    margin-top: 2px
}

.block--blog h2 a,
.category-blog h1,
h1.author-title {
    color: $color-blog
}



.block--features h2 a,
.category-features h1 {
    color: $color-features;
}

.block--training h2 a,
.category-training h1 {
    color: $color-about;
}

.block--jobs h2 a,
.category-jobs h1,
.block--jobs h2 a,
.category-jobs-admin h1 {
    color: $color-jobs;
}

.category-about h1.title {
    color: $color-about;
}

.category-groups h1 {
    color: $color-groups;
}

.category-events h1 {
    color: $color-events;
}

.category-boost h1 {
    color: $color-boost;
}

.category-events h1,
#tribe-community-events h2 {
    color: $color-events;
}

