/* Pages, events, jobs */

.single-job_listing h1.title,
.page-content h1.title,
.single-tribe_events h1.tribe-events-single-event-title {
    padding-top: 21px
}

.tribe-common .event-disclaimer h2 {
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: $font-weight-regular;
}


h3,
.tribe-events-single-event-description.tribe-events-content h3 {
    margin-top: 30px;
    margin-bottom: 20px;
    font-weight: $font-weight-regular;
    font-size: $font-size-h3;
}

/* --- site description --- */
#logo #site-description {
    color: $color-secondary;
    font-size: 18px;
    line-height: 26px;
    position: relative;
    left: 1px
}


/* --- overlapping title --- */
.single-post .container_inner.overlap,
.home .container_inner.overlap {
    display: block;
    position: absolute;
    bottom: 1px;
}

.overlap-img-title img {
    width: 100%;
    height: auto;
}

.overlap-img-title {
    display: block;
    position: relative;
    min-height: 100px;
}

.overlap-title {
    bottom: 0px;
    position: absolute;
}

.size-large.wp-post-image {
    width: 100%;
    height: auto;
}

.overlap-title h1.title {
    color: $color-copy;
}

.home .overlap-title h1.title {
    text-align: right;
}

.single-post .overlap-title h1.title,
.home .overlap-title h1.title {
    background-color: $color-overlay;
    display: inline-block;
    font-size: $font-size-h1-content;
    line-height: $line-height-h1-content;
    margin-right: 5px;
    vertical-align: center;
    padding: 4px 20px 13px 20px;
}


.skt-section .overlap-img-title a:hover,
.skt-section .overlap-img-title a:active,
.featured-image-shadow-box a:hover {
    box-shadow: 0px 0px 12px #8c8a8a;
    background: none;
}

.skt-section .overlap-img-title a {
    display: block;
    line-height: 0;
}

/* .home .promo {
	margin-left: 10px;
	margin-right: 10px;
} */

.row-fluid .promo {
    width: auto;
}

.home .overlap-title h1.title .fa-angle-right {
    margin-left: 20px;
    margin-right: 5px;
}

.home a:hover .overlap-title h1.title {
    /*color: $color-copy-reverse;*/
    background-color: $color-overlay;
    @include transition-0-5;
}


// --- home page --- 

.home-subhead {
    line-height: 1.2em;
}

a.subhead-link:link,
a.subhead-link:visited {
    color: $color-about;
}


// --- detail pages - jobs - events ---

.wpjm-listing-page h1.title {
    font-size: $font-size-h1-content;
}

.single-job_listing h1.title,
.category-groups h1.title {
    font-size: $font-size-h1-content;
    line-height: $line-height-h1-content;
    width: 85%;
}


// --- events ---

.single-tribe_events h1.tribe-events-single-event-title {
    color: $color-events
}

h2.tribe-events-page-title {
    font-weight: $font-weight-light;
}