.clear {
    clear: both;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
    background: transparent;
    border: 0;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
}

body {
    color: #353535;
    font-family: "Open Sans", Arial, sans-serif;
    font-size: 18px;
    line-height: 26px;
    font-weight: 300;
}

p,
dl,
hr,
h1,
h4,
h5,
h6,
ol,
ul,
pre,
table,
address,
fieldset,
figure {
    font-weight: $font-weight-light;
    margin-bottom: 20px;
    line-height: $line-height-body;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #555555;
    letter-spacing: -0.5px;
}

h2,
.tribe-events-single-event-description.tribe-events-content h2 {
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: $font-weight-regular;
    font-size: $font-size-h2;
    line-height: ($font-size-h2*1.3);
}

pre {
    overflow: auto;
    width: 98%;
}

embed {
    max-width: 100%;
}

ol {
    list-style: decimal outside none;
}

ul {
    list-style-type: disc;
}

li {
    margin-left: 30px;
}

dl {
    margin: 0 0 24px;
    float: left;
}

dt {
    font-weight: $font-weight-regular;
}

dd {
    margin-bottom: 18px;
}

cite {
    font-weight: $font-weight-regular;
}

//blockquote cite a{color: #FFFFFF;}
iframe {
    max-width: 100%;
    width: 100%;
    outline: none;
    border: none;
    overflow: hidden;
    border: none;
}

blockquote:before,
blockquote:after {
    content: '';
    content: none;
}

del {
    text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

th {
    color: #000;
    text-align: center;
    font-weight: $font-weight-regular;
}

th,
td {
    padding: 3px 5px;
    border: 1px solid #CCCCCC;
}

a img {
    border: none;
}

a {
    text-decoration: none;
    color: #FFA500;
}

a,
img,
a img,
input[type="text"],
input[type="password"],
input,
textarea {
    outline: none;
}

a:hover {
    color: #555;
}


