.skt-section {
    margin-top: 35px;
}

.skt-iconbox.iconbox-top {
    text-align: center;
}

.skt-iconbox.iconbox-top p {
    margin: 0;
}

.skt-iconbox.iconbox-top .iconbox-icon {
    height: auto;
    position: relative;
    text-align: center;
}

.iconbox-content {
    line-height: 22px;
    margin-top: 45px;
}

.skt-animated.skt-viewport.small-to-large {
    animation: skt_zoom 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s normal none 1;
    -webkit-animation: skt_zoom 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s normal none 1;
    -moz-animation: skt_zoom 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s normal none 1;
    -o-animation: skt_zoom 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s normal none 1;
    opacity: 1;
}

@keyframes skt_zoom {
    0% {
        transform: scale(.3);
        opacity: .1
    }

    100% {
        transform: scale(1);
        opacity: 1
    }
}

@-webkit-keyframes skt_zoom {
    0% {
        -webkit-transform: scale(.3);
        opacity: .1
    }

    100% {
        -webkit-transform: scale(1);
        opacity: 1
    }
}

@-moz-keyframes skt_zoom {
    0% {
        -moz-transform: scale(.3);
        opacity: .1
    }

    100% {
        -moz-transform: scale(1);
        opacity: 1
    }
}

@-o-keyframes skt_zoom {
    0% {
        -o-transform: scale(.3);
        opacity: .1
    }

    100% {
        -o-transform: scale(1);
        opacity: 1
    }
}

.iconbox-icon i {
    color: #fff;
    font-size: 34px;
    line-height: 54px;
    text-align: center;
    @include transition-0-3;
}

.skt-iconbox.iconbox-top .iconbox-icon img {
    top: 0;
    position: relative;
    top: 0;
    height: 150px;
    position: relative;
    width: 150px;
    margin-bottom: 0px;
    transition-duration: 0.5s;
    transition-timing-function: ease;
    transition-delay: 0s;
    -moz-transition-duration: 0.5s;
    -moz-transition-timing-function: ease;
    -moz-transition-delay: 0s;
    -webkit-transition-duration: 0.5s;
    -webkit-transition-timing-function: ease;
    -webkit-transition-delay: 0s;
    -o-transition-duration: 0.5s;
    -o-transition-timing-function: ease;
    -o-transition-delay: 0s;
}

a.skt-featured-icons {
    background: none repeat scroll 0 0 #FFA500;
    display: inline-block;
    height: 51px;
    position: relative;
    width: 88px;
}

.iconbox-icon a.skt-featured-icons i {
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
}

a.skt-featured-icons:before {
    border-bottom: 25px solid #FFA500;
    border-left: 44px solid rgba(0, 0, 0, 0);
    border-right: 44px solid rgba(0, 0, 0, 0);
    content: "";
    height: 0;
    left: 0;
    position: absolute;
    top: -25px;
    width: 0;
}

a.skt-featured-icons:after {
    border-left: 44px solid rgba(0, 0, 0, 0);
    border-right: 44px solid rgba(0, 0, 0, 0);
    border-top: 25px solid #FFA500;
    bottom: -25px;
    content: "";
    height: 0;
    left: 0;
    position: absolute;
    width: 0;
}

.skt-featured-images {
    display: block;
    line-height: 0;
    position: relative;
    margin: -25px 0;
}

.skt-featured-image-mask {
    background: url("images/Advertica-Hexagonal-Mask.png") repeat scroll 0 0 rgba(0, 0, 0, 0);
    display: block;
    height: 150px;
    position: absolute;
    width: 150px;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 10;
}

.skt-iconbox.iconbox-top .iconbox-content h4:after {
    background-color: $color-secondary;
    bottom: 0;
    content: "";
    display: block;
    height: 1px;
    left: 50%;
    margin-left: -25px;
    position: absolute;
    width: 50px;
}

.skt-iconbox.iconbox-top .iconbox-content h4 {
    margin-bottom: 20px;
    padding-bottom: 20px;
}

.skt-iconbox .iconbox-content h4 {
    font-size: 19px;
    position: relative;
    text-transform: capitalize;
}

.skt-parallax-button {
    border: 1px solid #F54336;
    cursor: pointer;
    display: inline-block;
    font-weight: $font-weight-bold;
    font-size: 12px;
    letter-spacing: 1px;
    line-height: 20px;
    margin-bottom: 0;
    padding: 8px 25px;
    position: relative;
    text-transform: uppercase;
    @include transition-0-3;
}

.skt-parallax-button:hover {
    background: #F54336;
    color: #fff;
    @include transition-0-3;
}

.iconbox-content h4 {
    transition: all .5s ease 0s;
    -webkit-transition: all .5s ease 0s;
    -moz-transition: all .5s ease 0s;
    -o-transition: all .5s ease 0s;
}

.mid-box-mid .mid-box:hover .iconbox-content h4 {
    transition: all .5s ease 0s;
    -webkit-transition: all .5s ease 0s;
    -moz-transition: all .5s ease 0s;
    -o-transition: all .5s ease 0s;
}

