    content: none;
}

.ske-footer-container .menu li {
    display: block;
    float: none;
}

.ske-footer-container .menu {
    float: none;
    width: 100%;
}

/* =Slider ends
-------------------------------------------------------------- */
.featured-image {
    display: block;
    position: relative;
    text-align: center;
    width: 100%;
    z-index: 1;
}

.featured-image-shadow-box {
    overflow: hidden;
    margin-bottom: 22px;
}

.featured_image_shadow {
    margin-top: -25px;
    position: relative;
    z-index: 0;
}

.portfolio-items {
    font-weight: bold;
    padding: 10px 4px 0;
}

ul.childpages {
    list-style-type: disc;
    margin-left: 20px;
}

.childpages li a {
    text-decoration: none;
}

.childpages li a:hover {
    padding: 0 2px;
}

/* =Pagination
-------------------------------------------------------------- */
#advertica-paginate {
    display: inline-block;
    font-size: 13px;
    margin: 20px 0;
    position: relative;
    text-align: center;
    width: 100%;
}

#advertica-paginate a {
    border: 1px solid #E0DEDE;
    display: inline-block;
    height: 32px;
    line-height: 31px;
    padding: 0;
    width: 32px;
    transition: all 1s ease 0s;
    -webkit-transition: all 1s ease 0s;
    -moz-transition: all 1s ease 0s;
    -o-transition: all 1s ease 0s;
}

#advertica-paginate a:hover {
    background: none repeat scroll 0 0 #F54336;
    color: #FFFFFF;
    transition: all 1s ease 0s;
    -webkit-transition: all 1s ease 0s;
    -moz-transition: all 1s ease 0s;
    -o-transition: all 1s ease 0s;
}

#advertica-paginate .advertica-prev {
    float: left;
    left: 0;
    margin: 0 3px 6px 0;
    position: absolute;
}

#advertica-paginate .advertica-next {
    float: right;
    margin: 0 0 6px 3px;
    position: absolute;
    right: 0;
}

#advertica-paginate .advertica-page {
    margin: 0 3px 6px;
}

#advertica-paginate .icon-angle-right,
#advertica-paginate .icon-angle-left {
    font-size: 24px;
    line-height: 30px;
}

#advertica-paginate .advertica-current {
    background: none repeat scroll 0 0 #F54336;
    color: #FFFFFF;
    border: 1px solid #E0DEDE;
    display: inline-block;
    height: 32px;
    line-height: 31px;
    padding: 0;
    width: 32px;
    transition: all 1s ease 0s;
    -webkit-transition: all 1s ease 0s;
    -moz-transition: all 1s ease 0s;
    -o-transition: all 1s ease 0s;
}

.navigation {
    color: #888888;
    font-size: 13px;
    line-height: 18px;
    margin: 10px 0 0;
    overflow: hidden;
}

.category .blog-navigation,
.tag .blog-navigation,
.archive .blog-navigation,
.search .blog-navigation {
    margin-bottom: 35px;
}

.nav-previous {
    float: left;
    width: 50%;
}

.nav-next {
    float: right;
    text-align: right;
    width: 50%;
}

